.categorySelect {
  display: flex;
  flex-flow: column;
}
.categorySelect > .buttonGroup {
  display: flex;
}
.categorySelect > .buttonGroup > label {
  flex: 1;
  text-align: center;
  border: solid 1px black;
  padding: 8px;
}
.categorySelect > .buttonGroup > label > input {
  display: none;
}
.categorySelect > .buttonGroup > label.checked {
  background-color: cyan;
}
.categorySelect > .perCapita > label.checked {
  background-color: lime;
}
